@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;900&family=PT+Serif&display=swap);
body {
  margin: 0;
  font-family: 'PT Serif', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

.wmde-markdown {
  font-family: 'PT Serif', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  border: none;
  background: transparent;
  cursor: pointer;
}

ol {
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Montserrat', sans-serif;
  color: inherit;
}

button > h1 {
  color: #fdfdfd;
  outline: none;
  border-bottom: 1px solid transparent;
  padding-bottom: 8px;
}

button > h1:hover {
  border-color: #fdfdfd;
}

p {
  font-family: 'PT Serif', serif;
  font-size: 1.2rem;
}

section {
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
}

li {
  font-size: 18px;
  padding: 4px;
  margin: 4px;
  cursor: pointer;
}

li > button {
  padding: 4px;
  border-bottom: 1px solid #000;
  transition: 0.3 s all ease-in-out; 
}

li > button:hover {
  background-color: #000;
  color: #fdfdfd;
  border-bottom: 1px solid #fdfdfd;
}

a {
  text-decoration: none;
}

article {
  padding: 40px 120px;
}

img {
  width: 800px;
}

.App {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header {
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    height: 200px;
    font-size: 48px;
    font-family: serif;
    background-color: #000;
    color: #fdfdfd;
}

.header > button {
    font-family: 'Montserrat', sans-serif;
    color: #fdfdfd;
    outline: none;
    border-bottom: 1px solid transparent;
    padding-bottom: 8px;
}

.header > button:hover {
    border-color: #fdfdfd;
}

.App > section {
    margin-left: 120px;
}

